.fileView {
    width: 80%;
}

.fileView__row{
    height: 250px;
    display: flex;
    align-items: center;
}
.fileView__titles {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
    color: rgb(85, 78, 78);
}

.fileView__titles>div>* {
    margin: 5px;
}
.fileView__titles--left {
    flex: 1;
}

.fileView__titles--right {
    display: flex;
}
