.newFile {
    display: flex;
    align-items: center;
    padding: 12px 0;
    padding-left: 20px;
}

.newFile_container{
    display: flex;
    justify-items: center;
    align-items: center;
    padding: 6px 32px 6px 8px;
    border-radius: 50px;
    box-shadow: 0 1px 2px 0 rgba(60, 64, 67, 0.302), 0 1px 3px 1px rgba(60, 64, 67, 0.149);
    cursor: pointer;
}

.newFile_container .MuiSvgIcon-root{
    font-size: 35px;
}

.newFile_container>p {
    margin-left: 14px;
    font-size: 15px;
}